}
.no-data{width:100%;padding:10% 0;color:#eaeaea;display:inline-block;font-size:18px;text-align:center
}
.tag{padding:5px 6px;font-size:10px;font-weight:400;border-radius:4px;background-color:#e7e7e7;text-align:center;vertical-align:middle
}
.week-alert{position:fixed;z-index:2600;top:0;left:0;right:0;text-align:center;background-color:rgba(75,75,75,0.85);color:#fff;padding:9px 20px;font-size:15px
}
.week-alert.alert-show{-webkit-animation:show 2s;animation:show 2s
}
@-webkit-keyframes show{
0%{-webkit-transform:translateY(-100%);transform:translateY(-100%)
}
20%{-webkit-transform:translateY(0);transform:translateY(0)
}
50%{-webkit-transform:translateY(0);transform:translateY(0)
}
80%{-webkit-transform:translateY(0);transform:translateY(0)
}
100%{-webkit-transform:translateY(-100%);transform:translateY(-100%)
}
}
@keyframes show{
0%{-webkit-transform:translateY(-100%);transform:translateY(-100%)
}
20%{-webkit-transform:translateY(0);transform:translateY(0)
}
50%{-webkit-transform:translateY(0);transform:translateY(0)
}
80%{-webkit-transform:translateY(0);transform:translateY(0)
}
100%{-webkit-transform:translateY(-100%);transform:translateY(-100%)
}
}
#brim-mask{pointer-events:none
}
.show-menu{-webkit-transform:translateX(320px);transform:translateX(320px)
}
