}
.user .tab{display:inline-block;width:40px;color:#626262;border-radius:8px;background-color:#e7e7e7;margin-right:16px;text-align:center;height:24px;line-height:24px;vertical-align:middle
}
.user .good{background-color:#80bd01;color:#fff
}
.user .title{font-size:16px;font-weight:bold;display:block;width:100%;-webkit-box-flex:1;-webkit-flex:1;-ms-flex:1;flex:1
}
